* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

header {
  background: #ffffff;
  box-shadow: 0px 0px 16px rgba(38, 38, 38, 0.16);
  padding: 16px 8px;
  margin-bottom: 32px;
}

.header-logo {
  padding-left: 8px;
}

.header_buttons {
  align-items: center;
  display: flex;
}

.header_button1 {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  font-weight: 500;
  font-size: 11px;
  padding: 13px 19px;
}

.header_button2 {
  background-color: #262626;
  border: 1px solid #262626;
  border-radius: 2px;
  font-weight: 500;
  color: #fff;
  font-size: 11px;
  margin-left: 11px;
  padding: 13px 19px;
}

.header_buttons_cart {
  margin-left: 11px;
}

.header-loc {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-loc-button_asap {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  font-weight: 500;
  padding: 12px 11px;
  font-size: 13px;
  color: #1d1d1d;
}

.header-loc-p {
  margin: 0 5px;
}

.header-loc-input {
  border: 1px solid #e0e0e0;
  padding-left: 11px;
  display: flex;
  width: 100%;
}

.header-loc-input_input {
  border: none;
  padding: 10px 25px 10px 15px;
  width: 100%;
}

.justify-content-between {
  display: flex;
  justify-content: space-between;
}

.search {
  margin-bottom: 13px;
}

.search-searchbar {
  border-bottom: 1px solid #757575;
  padding: 0px 8px 0 8px;
  width: 100%;
  display: flex;
}

.search-searchbar_input {
  border: none;
  padding: 16px 0;
  width: 100%;
}

.icon-search {
  margin-right: 10px;
}

input::placeholder {
  opacity: 0%;
  color: #d6d6d6;
}

.whento-asap {
  margin-right: 16px;
}

.whento_p {
  color: #626262;
}

.whento_button {
  background-color: #fff;
  color: #626262;
  border: 1px solid #e0e0e0;
  padding: 10px;
}

.whento_align {
  display: flex;
  width: 100%;
}

.whento-loc {
  width: 100%;
}

.whento-loc-input {
  border: 1px solid #e0e0e0;
  padding-left: 11px;
  display: flex;
  width: 100%;
}

.whento-loc-input_input {
  border: none;
  padding: 10px 0px 10px 15px;
  width: 100%;
}

.cards {
  margin-top: 27px;
  margin-bottom: 48px;
}

.cards_caption {
  color: #1d1d1d;
  font-size: 22px;
}

.cards-rest {
  width: 100%;
  margin-bottom: 28px;
}

.cards-rest_img {
  width: 100%;
}

.cards-rest_name {
  color: #1d1d1d;
  font-size: 16px;
}

.cards-rest_type {
  color: #626262;
  font-size: 14px;
}

.cards-rest_time {
  color: #1d1d1d;
  font-size: 13px;
}

footer {
  background-color: #262626;
  position: relative;
}

.pageup {
  z-index: 100;
  display: block;
  position: absolute;
  right: 3%;
  top: 82%;
}

.footer-logo {
  padding: 56px 0px 40px 8px;
  border-bottom: 1px solid #979797;
}

.footer-language_wrapper {
  padding: 13px 0 13px 25px;
  background-color: #3b3b3b;
  display: flex;
  border-radius: 2px;
  width: 100%;
}

.footer-language_select {
  color: white;
  width: 100%;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  background: url("/img/arrowdown.svg");
  background-repeat: no-repeat;
  background-position: 90% center;
}

.footer-language_select option {
  background-color: #3b3b3b;
}

.footer-social {
  margin-top: 32px;
}

.footer-social-icons {
  width: 100%;
  display: flex;
  margin-top: 16px;
  margin-bottom: 16px;
}

.footer-social-tag_link {
  color: #49a144;
  text-decoration: none;
}

.footer-social-tag_link_span {
  color: white;
}

.footer-social-logo {
  text-decoration: none;
  margin-right: 7px;
}

.footer-nav_link {
  text-decoration: none;
  color: white;
  margin-bottom: 30px;
  font-size: 13px;
}

.justify-center {
  justify-content: center;
}

.footer-apps {
  padding: 21px 0;
  border-top: 1px solid #979797;
  border-bottom: 1px solid #979797;
  display: flex;
  justify-content: center;
}

.footer-apps img {
  margin: 0 7px;
}

.footer-privacy {
  padding: 42px 0px 22px 0px;
}

.footer-privacy_link {
  color: white;
  text-decoration: none;
  font-size: 11px;
  display: block;
  margin-bottom: 20px;
}

.test {
  flex-flow: row wrap;
  padding: 40px 0 24px 0;
}

.footer-nav {
  display: flex;
  align-content: space-between;
  flex-direction: column;
}

.footer-privacy-adaptive {
  display: flex;
  flex-direction: column;
}

.footer-privacy_link-margin {
  margin-top: 20px;
  margin-bottom: 20px;
}

.testtest {
  display: flex;
  flex-direction: column;
  width: 100%;
}

@media only screen and (min-width: 576px) {
  .container {
    width: 560px;
    max-width: 100%;
  }
  p {
    font-size: 11px;
  }
  .header_button1,
  .header_button2 {
    font-size: 11px;
    padding: 13px 19px;
  }
  .flex-column {
    flex-direction: column;
  }
  .footer-privacy-adaptive {
    flex-direction: column;
  }
  .footer-social {
    margin-bottom: 40px;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    width: 752px;
    max-width: 100%;
  }
  p {
    font-size: 13px;
  }
  .header_button1,
  .header_button2 {
    font-size: 11px;
    padding: 11px 19px;
  }
  .footer-privacy {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
  .footer-privacy-adaptive {
    flex-direction: row !important;
  }

}

@media only screen and (min-width: 992px) {
  .container {
    width: 976px;
    max-width: 100%;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    width: 1184px;
    max-width: 100%;
  }
  .footer-apps {
    padding: 40px 0 !important;
  }
 
}